.input{position:relative;box-sizing:border-box}.input__field{display:block;width:100%;padding:0.8rem 1rem;font-size:1rem;font-weight:600;border:1px solid #21125e;outline:none;box-shadow:0 0 0 1px #fff;background-color:transparent}.input__field:focus{box-shadow:0 0 0 1px #21125e}.input__field--error{border:1px solid #c22d74}.input__field--error:focus{box-shadow:0 0 0 1px #c22d74}.input__field--left-icon{padding-left:3rem}.input__label{font-size:.875rem;color:#7d7d7d;margin-bottom:.5rem;display:inline-block;font-weight:600}.input__error{color:#c22d74;font-size:.875rem}.input__help-text{color:#7d7d7d;font-size:.875rem}.input__icon-left{position:absolute;left:1rem;top:50%;transform:translate(0, -50%)}.input__icon-right{position:absolute;right:1rem;top:50%;transform:translate(0, -50%)}
