.checkut-review__content{margin:0 0 3rem 3rem}.checkut-review__content__summary{display:grid;grid-template-columns:1fr;grid-gap:2rem;margin-top:5rem}@media (min-width: 540px){.checkut-review__content__summary{grid-template-columns:1fr 1fr}}@media (min-width: 992px){.checkut-review__content__summary{grid-template-columns:1fr 1fr 1fr 1fr}}.checkut-review__content__summary h4{font-weight:600;text-transform:uppercase;margin-bottom:1rem;padding-bottom:1rem;border-bottom:1px solid #f6f6f6}.checkut-review__content__thumbnail img{width:50px;height:auto}.checkut-review__content__submit{text-align:right;margin-top:6rem}
