.footer__newsletter{border-top:1px solid #f6f6f6;border-bottom:1px solid #f6f6f6;padding:2rem 0}@media (min-width: 992px){.footer__newsletter .container{display:flex;justify-content:space-between;align-items:center;color:#323232}}.footer__newsletter .container h4{color:#7d7d7d;font-weight:600}@media (min-width: 992px){.footer__newsletter .container h4{font-size:1.5rem;color:#323232}}.footer__newsletter .container button{margin:2rem auto 0;display:block}@media (min-width: 992px){.footer__newsletter .container button{margin:0 2rem 0 0}}.footer__favicons{display:flex;justify-content:center}.footer__menu{background-color:#f6f6f6;padding:3rem 0 2rem}.footer__menu .container{display:flex;flex-wrap:wrap}.footer__menu .container div:not(:last-of-type){padding-right:3rem}.footer__menu-section-header{font-weight:600;margin-bottom:1rem}.footer__menu-section-content p{font-weight:600;margin-bottom:1rem;font-size:.875rem;color:#7d7d7d}.footer__menu-section-content p a{text-decoration:none;color:#7d7d7d}.footer__menu-section-content--split{display:grid;column-gap:4rem;grid-template-columns:repeat(2, 1fr)}
