@keyframes sidenavLeftOpen{from{left:-100rem}to{left:0}}@keyframes sidenavRightOpen{from{right:-100rem}to{right:0}}.overlay{position:fixed;top:0;height:100%;min-height:100vh;width:100%;background-color:rgba(199,207,207,0.8);transition:300ms}.overlay--search{top:3.55rem}.overlay--search .overlay__right{background-color:transparent;height:auto}.overlay--no-background{background-color:transparent}.overlay__left{position:fixed;min-height:100%;background-color:#fff;animation:sidenavLeftOpen 0.3s}.overlay__right{position:fixed;background-color:#fff;right:0;top:0;height:100%;animation:sidenavRightOpen 0.3s}.overlay__header{align-items:center;border-bottom:1px solid #f6f6f6;display:flex;flex:1;font-weight:600;height:3.6rem;justify-content:space-between;text-transform:uppercase}.overlay__header-text{margin:0 1rem;text-align:center}.overlay__header-text-items{color:#7d7d7d}.overlay__header__close-icon{cursor:pointer;margin:0 1rem}.overlay__header__close-icon:hover svg *,.overlay__header__close-icon:focus svg *{fill:#51e9d2}.overlay__header__close-icon div{display:flex}.overlay__header__cart-icon{margin:0 1rem}.overlay__header__cart-icon div{display:flex}.overlay__header__cart-icon div svg *{fill:#51e9d2}
