.cart{width:25rem;max-width:100vw}.cart__list{padding:0 1rem;height:calc(100vh - 17.5rem);overflow:auto}.cart__list__item{padding:1rem 0;border-bottom:1px solid #f6f6f6;display:flex;align-content:stretch}.cart__list__item img{max-width:6.25rem;height:6.25rem}.cart__list__item__details{width:100%;padding:0 1rem}.cart__list__item__details p{font-weight:600;text-transform:uppercase;line-height:1.5}.cart__list__item__details__variant{display:flex;justify-content:space-between;margin-top:.5rem}.cart__list__item__details__variant span{color:#7d7d7d;font-size:.875rem}.cart__list__item__details__delete-icon{background-color:#f6f6f6;border-radius:1rem;width:1.5rem;height:1.5rem;text-align:center;line-height:1.5rem;margin-top:.5rem;cursor:pointer}.cart__list__item__details__delete-icon svg *{transition:0.3s}.cart__list__item__details__delete-icon:hover svg *{fill:#51e9d2}.cart__footer{background-color:#f6f6f6;padding:1rem;position:fixed;bottom:0;width:25rem}.cart__footer__subtotoal{display:flex;padding-bottom:1rem;text-transform:uppercase;font-weight:600;justify-content:space-between;border-bottom:1px solid #7d7d7d}.cart__footer__button{margin:1rem auto 1rem;text-align:center}.cart__empty{padding:2rem 1rem}.cart__empty h4{font-weight:600;text-transform:uppercase;margin-bottom:1rem}.cart__empty p{color:#7d7d7d}.cart__empty__action{text-align:center;margin-top:1rem}
