.side-nav{width:30rem;max-width:calc(100vw - 5rem)}.side-nav__menu-item{border-bottom:1px solid #f6f6f6}.side-nav__menu-item a{align-items:center;color:#323232;display:flex;font-weight:600;height:calc(3.6rem - 1px);text-decoration:none;text-transform:uppercase;transition:0.3s}.side-nav__menu-item a:hover{color:#51e9d2}.side-nav__menu-item a svg *{fill:currentColor}.side-nav__menu-item--parent{color:#7d7d7d;display:grid;grid-template-columns:1fr 3.6rem}.side-nav__menu-item--parent a{color:inherit}.side-nav__menu-item-back div{display:flex}.side-nav__menu-item-back svg{margin-right:1rem}.side-nav__menu-item-close{align-items:center;cursor:pointer;display:flex;justify-content:center}.side-nav__menu-item-close span{background-color:currentColor;border-radius:1px;height:3px;width:24px}.side-nav__menu-item-close:hover{color:#51e9d2}.side-nav__menu-item-close--noback{height:3.6rem}.side-nav__menu-item-label{display:flex;margin:0 1rem}.side-nav__icon-item div{display:inline-block;margin-right:.5rem;vertical-align:middle}.side-nav__icon-item div svg *{transition:0.3s}
