.products-list__loader{position:relative;left:68px}.products-list__products__subheader{display:flex;justify-content:space-between;flex-wrap:wrap;font-size:.875rem;align-items:center}.products-list__products__subheader__total{color:#7d7d7d;margin:1rem 0}.products-list__products__subheader__sort{display:flex;align-items:center}.products-list__products__subheader__sort span{color:#7d7d7d}.products-list__products__subheader__sort .dropdown-component{min-width:10rem}.products-list__products__grid{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:2rem;grid-row-gap:2.5rem}@media (min-width: 540px){.products-list__products__grid{grid-template-columns:1fr 1fr}}@media (min-width: 992px){.products-list__products__grid{grid-template-columns:1fr 1fr 1fr 1fr;grid-row-gap:2rem}}.products-list__products__grid a{text-decoration:none;color:inherit}.products-list__products__load-more{margin:5rem 0;text-align:center}.products-list__products-not-found{margin-bottom:4rem}
