.main-menu{display:flex;align-items:center;border-bottom:1px solid #f6f6f6}.main-menu__left{flex-basis:calc(50% - 2rem)}@media (min-width: 540px){.main-menu__left{flex-basis:calc(50% - 4rem)}}.main-menu__left ul{display:flex;align-items:center}.main-menu__right{flex-basis:calc(50% - 2rem)}@media (min-width: 540px){.main-menu__right{flex-basis:calc(50% - 4rem)}}.main-menu__right ul{display:flex;justify-content:flex-end;align-items:center}.main-menu__center svg{width:4rem}@media (min-width: 540px){.main-menu__center svg{width:6rem}}.main-menu__item{margin:0 1.5rem;text-transform:uppercase;font-weight:600}.main-menu__item a{text-decoration:none;color:#323232;position:relative;transition:0.3s}.main-menu__item a:hover{color:#51e9d2}.main-menu__hamburger{border-right:1px solid #f6f6f6;cursor:pointer;padding:1rem}.main-menu__hamburger--hover{display:none}.main-menu__hamburger:hover .main-menu__hamburger--icon{display:none}.main-menu__hamburger:hover .main-menu__hamburger--hover{display:block}.main-menu__hamburger svg{vertical-align:bottom}.main-menu__icon{padding:0 1rem;cursor:pointer}.main-menu__icon svg *{transition:0.3s}.main-menu__icon:hover svg *{fill:#51e9d2}.main-menu__icon svg{vertical-align:bottom}.main-menu__cart{position:relative}.main-menu__cart__quantity{position:absolute;top:-0.3rem;right:0.6rem;background-color:#21125e;border-radius:1rem;width:1rem;height:1rem;color:#fff;font-size:.875rem;text-align:center;line-height:1rem;transition:0.3s}.main-menu__cart:hover span{background-color:#51e9d2}.main-menu__user--active *{fill:#51e9d2;fill-rule:inherit}.main-menu__dropdown{text-transform:uppercase;font-weight:600}.main-menu__dropdown li{cursor:pointer}.main-menu__dropdown li:hover{color:#7d7d7d}.main-menu__dropdown li:not(:last-of-type){border-bottom:1px solid #f6f6f6}.main-menu__offline{line-height:24px;padding:1rem}.main-menu__offline span{text-transform:uppercase;font-weight:600;vertical-align:bottom}.main-menu__search{padding:1rem;cursor:pointer;line-height:24px;transition:0.3s}@media (min-width: 992px){.main-menu__search{border-left:1px solid #f6f6f6}}.main-menu__search svg *{transition:0.3s}.main-menu__search:hover{color:#51e9d2}.main-menu__search:hover svg *{fill:#51e9d2}.main-menu__search span{text-transform:uppercase;font-weight:600;margin-right:1rem}.main-menu__search div{display:inline-block}.main-menu__search svg{vertical-align:bottom}
