.product-page{background-color:#f1f5f5}.product-page .breadcrumbs{background-color:#fff;padding:.5rem 1rem .5rem 1.5rem;margin-top:2rem}.product-page__product{display:flex;margin-top:1rem;padding-bottom:5rem}@media (max-width: 540px){.product-page__product{display:block;margin-bottom:2rem}}.product-page__product__info{flex-grow:1;width:40%;position:relative}@media (max-width: 992px){.product-page__product__info{width:50%}}@media (max-width: 540px){.product-page__product__info{width:100%}}.product-page__product__info--fixed{position:fixed;width:auto;padding-right:1rem}@media (min-width: 1140px){.product-page__product__info--fixed{width:calc(1140px * 40 / 100)}}.product-page__product__info--absolute{position:absolute;width:100%;bottom:0}.product-page__product__gallery{flex-grow:2;width:60%;padding:0 1rem 0 0;box-sizing:border-box}@media (max-width: 992px){.product-page__product__gallery{width:50%}}@media (max-width: 540px){.product-page__product__gallery{width:100%;padding:0;margin-bottom:2rem;padding:0}}.product-page__product__gallery img{width:100%}.product-page__product__gallery img:not(:last-of-type){margin-bottom:5rem}@media (max-width: 540px){.product-page__product__gallery img{height:calc(100vw - 2rem);margin-bottom:2rem}}.product-page__product__gallery__nav li{display:inline-block;padding:.5rem}.product-page__product__gallery__nav li span{width:.5rem;height:.5rem;border:1px solid #7d7d7d;border-radius:.5rem;display:block}.product-page__product__gallery__nav li.active span{background-color:#51e9d2;border-color:#51e9d2}.product-page__other-products{background-color:#fff;padding:4rem 0}.product-page__other-products__title{text-transform:uppercase;font-weight:600;margin-bottom:2rem}.product-page__other-products__grid{display:grid;grid-template-columns:1fr;grid-gap:2rem}@media (min-width: 540px){.product-page__other-products__grid{grid-template-columns:1fr 1fr}}@media (min-width: 992px){.product-page__other-products__grid{grid-template-columns:1fr 1fr 1fr 1fr}}
