.article-page__container{column-gap:1.5rem;display:grid;grid-template-columns:repeat(3, 1fr);margin:60px 0}@media (max-width: 992px){.article-page__container{display:block}}.article-page__header{text-align:center;padding-top:4.5rem;padding-bottom:4.5rem;background-size:cover;background-color:rgba(33,18,94,0.8);background-blend-mode:multiply}.article-page__header__title{background-color:#fff;display:inline-block;transform:skew(45deg);padding:1.5vw 9vw}.article-page__header__title h1{display:inline-block;transform:skew(-45deg);font-weight:600;text-transform:uppercase;color:#21125e;font-size:8vw;font-weight:900}@media (min-width: 540px){.article-page__header__title h1{font-size:4vw}}.article-page__content{grid-column-end:span 2}.article-page__content h1,.article-page__content h2,.article-page__content h3{font-weight:600}.article-page__content h2{font-size:1.5rem;margin-bottom:1.5rem}.article-page__content h3{font-size:1.2rem;margin-bottom:1rem}.article-page__content img{max-width:100%}@media (max-width: 992px){.article-page__navigation{margin-bottom:2rem}}.article-page__navigation-element{font-family:"Montserrat";font-size:1.5rem;font-weight:600;text-transform:uppercase}.article-page__navigation-element--active a{display:inline-block;position:relative}.article-page__navigation-element--active a:before{background-color:#51e9d2;content:"";height:7px;left:0;position:absolute;top:6px;transform:skew(45deg);width:100%;z-index:-1}
