.category__header{text-align:center;padding-top:4.5rem;padding-bottom:4.5rem;background-size:cover;background-color:rgba(33,18,94,0.8);background-blend-mode:multiply}.category__header__title{background-color:#fff;display:inline-block;transform:skew(-45deg);padding:1.5vw 9vw}.category__header__title h1{display:inline-block;transform:skew(45deg);font-weight:600;text-transform:uppercase;color:#21125e;font-size:8vw;font-weight:900}@media (min-width: 540px){.category__header__title h1{font-size:4vw}}
