.cart-summary{width:23rem;background-color:#f6f6f6;padding:1rem}.cart-summary p,.cart-summary h4{text-transform:uppercase;font-weight:600}.cart-summary__header{padding-bottom:1rem}.cart-summary__product-item{border-top:1px solid #7d7d7d;padding:1rem 0;display:flex;align-items:flex-start}.cart-summary__product-item img{width:4.6rem;height:4.6rem;margin-right:1rem}.cart-summary__product-item div{width:100%}.cart-summary__product-item__details{color:#7d7d7d;display:flex;justify-content:space-between}.cart-summary__product-item__details span{font-size:.875rem}.cart-summary__totals{display:flex;justify-content:space-between;padding:1rem 0;border-top:1px solid #7d7d7d}
